<template>
  <div class="mapwrapper">
       <yd-popup v-model="show1" position="center" width="90%">

        <yd-cell-group title="Filters">
            <yd-cell-item>
                <span slot="left"><p class="spinner-lable">Rooms</p> </span>
                <span slot="right"><yd-spinner max="3" unit="1" width="150px" height="40px" v-model="rooms"></yd-spinner></span>
            </yd-cell-item>

             <yd-cell-item>
                <span slot="left"><p class="spinner-lable">Bathrooms</p> </span>
                <span slot="right"><yd-spinner max="3" unit="1" width="150px" height="40px" v-model="rooms"></yd-spinner></span>
            </yd-cell-item>

             <yd-cell-item>
                <span slot="left"><p class="spinner-lable">Bathrooms</p> </span>
                <span slot="right"><yd-spinner max="3" unit="1" width="150px" height="40px" v-model="rooms"></yd-spinner></span>
            </yd-cell-item>

             <yd-cell-item>
                <span slot="left"><p class="spinner-lable">Bathrooms</p> </span>
                <span slot="right"><yd-spinner max="3" unit="1" width="150px" height="40px" v-model="rooms"></yd-spinner></span>
            </yd-cell-item>

          
        </yd-cell-group>
            

            <p style="text-align: center;">
                <yd-button @click.native="show1 = false">Close Center Popup</yd-button>
            </p>
        </yd-popup>
  </div>
</template>

<script>
import {Popup, Spinner, CellItem, CellGroup, Accordion, AccordionItem} from 'vue-ydui'
export default {
  data () {
    return {
        show1: true,
        rooms:0
    }
  },
  components : {
      Popup,
      Spinner,
      CellGroup,
      CellItem,
      AccordionItem,
      Accordion
  }
}
</script>

<style lang="stylus" scoped>
.mapwrapper
    .spinner-lable
        font-size : 20px
</style>    




